.r2s-contsiner {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    /* background-color: rgb(109, 132, 64); */
    padding: 20px 0px;
}

.r2s-heading {
    font-size: 36px;
    color: white;
    text-align: center;
}

.r2s-rankings {
    border-radius: 24px;
    padding: 9px 35px;
    background-color: white;
    color: rgb(109, 132, 64);
    font-size: 18px;
    font-weight: 500;
    margin-top: 15px;
    border: 1px solid transparent;
}

.r2s-rankings:hover {
    background-color: rgb(109, 132, 64);
    color: white;
    border: 1px solid white;
    transition: 0.5s;
    cursor: pointer;
}